import Heroine from "./heroine";
import Enemy from "./enemy";
import Attack from "./attack";
// import Attack from "./attack";

export default class Game {

    constructor(canvas){
        this.ctx = canvas.getContext("2d");
        this.dimensions = {width: canvas.width, height: canvas.height};
        this.player = new Heroine(this.dimensions);
        this.score = 0;
        this.enemies = [];
        this.enemyPositions = [];
        this.enemiesInterval = 600;
        this.gameOver = false;
        this.attacks = []
        this.attackAmount = 5;
        this.fire = false;
        this.health = 3;
        this.frameInterval = 0;
        this.restart();
        this.handleEvents();

        // when fire we add newAttack to attacks.length, attackAmount -=1
        // when a fire collides, then attackAmount += 1

    };

    play() {
        this.animate();
    }

    restart() {
        this.player = new Heroine(this.dimensions);
        this.score = 0;
        this.animate();
    };

    handleEvents() {
        window.addEventListener("keydown", this.eventDown.bind(this));
        window.addEventListener("keyup", this.eventUp.bind(this));
        // window.addEventListener("onkeydown", this.spaceBarDown.bind(this));
        // window.addEventListener("onkeyup", this.spaceBarUp.bind(this));
    }

    // spaceBarDown(e) {
    //     this.player.fireDown(e)
    // }

    // spaceBarUp(e) {
    //     this.player.fireUp(e)
    // }

    eventDown(e) {
        this.player.keyDown(e);        
    };

    eventUp(e) {
        this.player.keyUp(e);
    };

    movePlayer() {
        if (this.player.keys["ArrowDown"] && 
        this.player.y < this.dimensions.height - this.player.height) {
            this.player.y += this.player.speed;
            this.player.frameY = 0;
        };

        if (this.player.keys["ArrowUp"] && this.player.y > 100) {
            this.player.y -= this.player.speed;
            this.player.frameY = 3;
        };
    }

    detectCollision(first, second) {
        if (!(first.x > second.x + second.width ||
             first.x + first.width < second.x ||
             first.y > second.y + second.height || 
             first.y + first.height < second.y)    ) {
            return true;
        };
    }

    handleHeroine() {
        if (this.lifePoint <= 0) {
            this.gameOver = true;
        };
    };

    handleEnemies() {
        for (let i = 0; i < this.enemies.length; i++) {
            this.enemies[i].update(this.frame);
            this.enemies[i].draw(this.ctx);
            // this.enemies[i].handleFrame();
            if (this.enemies[i].lifePoints <= 0) {
                let addedScore = this.enemies[i].maxHealth;
                this.score += addedScore;
                const currentEnemyPosition = this.enemyPositions.indexOf(this.enemies.y)
                this.enemyPositions.splice(currentEnemyPosition, 1);
                this.enemies.splice(i, 1);
                i--;
            };
            // current test code:
            // if (this.enemies[i].x <= 100) {
            //     this.health -= 1;
            //     console.log("totalPoint:" + this.health);
            // };
        }

        if (this.frameInterval % 100 === 0) {
            let verticalPosition = Math.floor((Math.random() * 8) + 2) * 50;
            this.enemies.push(new Enemy(verticalPosition))
            if (this.enemiesInterval > 120) this.enemiesInterval -= 50;
        };
        
 
    }

    handleAttacks() {
        for (let i = 0; i < this.attacks.length; i++) {
            this.attacks[i].update();
            this.attacks[i].draw(this.ctx);

            for (let j = 0; j < this.enemies.length; j++) {
                if (this.enemies[j] && this.attacks[i] &&
                    this.detectCollision(this.attacks[i], this.enemies[j])) {
                    this.enemies[j].lifePoints -= this.attacks[i].damage;
                    // get rid of attack instance from array
                    // increase attackAmount += 1
                    this.attacks.splice(i, 1);
                    this.attackAmount += 1;
                    i--;
                }
            }

            if (this.attacks[i] && this.attacks[i].x >= this.dimensions.width) {
                this.attacks.splice(i, 1);
                this.attackAmount += 1;
                i--;
            }
        }
        if (this.player.attack && this.attackAmount > 0) {
            this.attacks.push(new Attack(this.player.x + 30, this.player.y+20))
            this.attackAmount -= 1;
        };
    }

    handleGameStatus() {
        if (this.gameOver) {
            console.log("GAME OVER!")
        }
        this.ctx.fillStyle = "gold";
        this.ctx.font = "30px Arial";
        this.ctx.fillText("Score: " + this.score, 20, 40);
    }





    animate() {
        this.ctx.clearRect(0, 0, this.dimensions.width, this.dimensions.height);
        this.movePlayer();
        this.handleHeroine();
        // this.handleEnemies();
        this.handleAttacks();
        this.handleGameStatus();
        this.player.handleFrame();
        this.player.animate(this.ctx);
        this.frameInterval++;
        requestAnimationFrame(this.animate.bind(this));
    };

    


};


   